import React from 'react'
import {
    SectionTitle,
    ItemList,
    Item,
    Date,
    BoldText
} from '../section-card/section-card.styles'
import CustomEmoji from '../custom-emoji/custom-emoji.component'

const Education = () => {

    return (
        <>
            <SectionTitle><CustomEmoji label='computer' emoji='🏫' /> Education</SectionTitle>
            <ItemList>
                <Item>
                   <p><CustomEmoji label='graduation' emoji='🎓' /> Graduated from <BoldText>Trayka University</BoldText> in Computer Engineering. <Date>(2022)</Date></p> 
                </Item>
            </ItemList>
        </>
    )

}

export default Education